
































































































































































































































































@import "@design";
$spacing: 4rem;

.logo {
  border: 2px solid $color-primary;
  padding: 1rem;
  display: flex;
  align-items: center;
  text-align: center;
  font-weight: bold;
  letter-spacing: 0.25rem;
  margin: 0;
  position: relative;
  font-size: 1rem;
  &__container {
    position: absolute;
    z-index: 0;
    left: $spacing;
    top: $spacing;
  }
  &__link {
    &:before {
      position: absolute;
      content: "";
      background-size: auto auto;
      background-image: repeating-linear-gradient(
        45deg,
        transparent,
        transparent 3px,
        $color-primary 3px,
        $color-primary 4px
      );
      z-index: -1;
      top: -10px;
      left: -10px;
      width: 100%;
      height: 100%;

      clip-path: polygon(
        0% 0%,
        0% 100%,
        10px 100%,
        10px 10px,
        100% 10px,
        100% 100%,
        100% 0%
      );
    }
  }
}
.container {
  padding: $spacing * 0.5 $spacing;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.gallery {
  display: grid;
  grid-template-columns: 2fr 1fr;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);

  &__item {
    min-height: 25vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    &:first-child {
      grid-row-start: 1;
      grid-row-end: span 2;
    }
  }
}
.checkins {
  display: flex;
  margin-bottom: 2rem;
  &__item {
    flex: 1;
  }
  &__time {
    font-size: 1.25rem;
  }
}
.amenities {
  background-color: #f0f0f0;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  &__item {
    width: calc(25% - 5px);
    margin: 0.5rem 0;
    font-size: 0.8rem;
    &--inactive {
      color: $color-text-secondary;
    }
  }
}
.sidebar {
  display: flex;
  justify-content: space-between;
}
.price {
  text-align: right;
  padding-right: 1rem;

  &__title:first-child {
    font-size: 1.5rem;
  }
  &__meta {
    font-size: 0.75rem;
    color: $color-text-secondary;
    margin-bottom: 0.75rem;
  }
}
.calendar {
  margin-bottom: 2rem;
  border: none;
  background-color: #f7f7f7;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
  font-family: $system-default-font-family;
  &__container {
    flex-basis: 60%;
  }
}

.form {
  label {
    line-height: 40px;
    flex-basis: 50%;
    flex-shrink: 0;
    font-size: 0.9rem;
  }
  &__item {
    display: flex;
    margin-bottom: 15px;
  }
  &__datepicker {
    width: 100%;
    input {
      box-shadow: none;
      border-color: $color-border-base;
      &:focus {
        box-shadow: none;
        border-color: $color-primary;
      }
    }
  }
  &__buttons {
    text-align: right;
    margin-top: 2.5rem;
  }
}
.summary {
  background: #ededed;
  margin: 0 -30px;
  padding: 10px 30px;
  display: flex;
  flex-wrap: wrap;
  color: lighten($color-primary, 40);
  font-size: 0.75rem;
  > span {
    flex: 50%;
    &:nth-child(even) {
      text-align: right;
    }
  }
}
.total {
  margin: 15px 0;
  text-align: right;
  color: $color-accent;
  font-size: 1.5rem;
}
